import { Utils } from 'weex-ui'
import Util from '@/utils'
import Const from '@/utils/const'
const modal = weex.requireModule('modal')
const storage = weex.requireModule('storage')

function pay (orderId, returnUrl) {
  storage.setItem(Const.KEY_IS_PAYING, '1', ({result}) => {
    if (result === 'success') {
      doPay(orderId, returnUrl)
    }
  })
}

async function doPay (orderId, returnUrl) {
  if (Utils.env.isWeb()) {
    try {
      const payGateway = await Util.ajax({
        method: 'POST',
        url: '/node_api/wapPay',
        data: {
          orderId: +orderId,
          returnUrl: returnUrl || location.href
        }
      })
      location.href = payGateway.url
    } catch (e) {
      console.error(e)
      modal.toast({
        message: '',
        duration: 3
      })
    }
  } else {
    // TODO Native Pay
  }
}

export default pay



