`<template>
  <div class="welcome-wrapper" :style="wrapperStyle">
    <div class="decorator"/>
    <image :src="backBtnSrc" @click="onBackClick" class="back-btn" :style="{opacity: step === 1 ? 0 : 1}" />
    <div class="upper" ref="welcomeUpper">
      <div class="upper-container" ref="upperOne">
        <text class="welcome-title-main"></text>
        <text class="welcome-title-desc"></text>
      </div>
      <div class="upper-container hidden" ref="upperTwo">
        <text class="welcome-title-main">{{loginMethod === 'password' ? '' : ''}}</text>
        <text class="welcome-title-desc" v-if="loginMethod === 'password'"></text>
        <text class="welcome-title-desc" v-else>{{region}} {{phone | friendlyPhone}}</text>
      </div>
    </div>
    <div class="frame">
      <div class="frame-wrapper" ref="welcomeFrame">
        <Welcome @nextStep="inputPhoneAndRegion" @showAgreement="toggleShowAgreement" />
        <Authorize ref="authorize" :login-method="loginMethod" @toggleLoginMethod="toggleLoginMethod" @attemptSnsLogin="attemptSnsLogin" @attemptPasswordLogin="attemptPasswordLogin" @resendSns="sendSns" />
      </div>
    </div>
    <wxc-mask height="800"
              width="702"
              border-radius="0"
              duration="200"
              mask-bg-color="#FFFFFF"
              :has-animation="false"
              :has-overlay="true"
              :overlay-can-close="false"
              :show-close="true"
              :show="showAgreement"
              @wxcMaskSetHidden="toggleShowAgreement">
      <part-loading v-if="isWebEnv" class="loading" :width="200" :height="200" :show="true"></part-loading>
      <web style="width: 702px;height: 800px;" src="https://pizza-express-app-1252752952.cos.ap-shanghai.myqcloud.com/%E8%B5%84%E8%B4%A8%E4%B8%8E%E8%A7%84%E5%88%99.html" />
    </wxc-mask>
    <wxc-loading :show="isGlobalLoadingShow"></wxc-loading>
  </div>
</template>

<script>
import { Utils, WxcMask, WxcLoading } from 'weex-ui'
import Util from '@/utils'
import Const from '@/utils/const'
import PartLoading from '@/common/PartLoading'
import Welcome from './components/Welcome'
import Authorize from './components/Authorize'
const modal = weex.requireModule('modal')
const statusBarChanger = weex.requireModule('StatusBarChanger')
const nativeNavigator = weex.requireModule('NativeNavigator')
const storage = weex.requireModule('storage')
const animation = weex.requireModule('animation')
const globalEvent = weex.requireModule('globalEvent')
export default {
  name: 'App',
  created () {
    if (Utils.env.isAndroid()) {
      statusBarChanger.enableFullscreen()
      globalEvent.addEventListener('AndroidBack', () => {
        if (this.step > 1) {
          this.back()
        } else if (this.showAgreement) {
          this.showAgreement = false
        } else {
          nativeNavigator.pop()
        }
      })
    }
    if (Utils.env.isWeb()) {
      window.addEventListener('popstate', this.unbind = () => {
        this.back()
      })
    }
    storage.getItem(Const.KEY_USER, ({result, data}) => {
      if (result === 'success' && JSON.parse(data).id) {
        Util.navigate('main')
      }
    })
  },
  data () {
    return {
      isGlobalLoadingShow: false,
      step: 1,
      region: '',
      phone: '',
      loginMethod: 'password',
      backBtnSrc: Util.getImg('back_light.png'),
      showAgreement: false,
      isWebEnv: Utils.env.isWeb()
    }
  },
  computed: {
    wrapperStyle () {
      const deviceStatusBarHeight = +(weex.config.env.statusBarHeight || 0)
      return {
        paddingTop: `${deviceStatusBarHeight * 750 / (+weex.config.env.deviceWidth)}px`
      }
    }
  },
  filters: {
    friendlyPhone (value) {
      const first = value.substring(0, 3)
      const middle = value.substring(3, 7)
      const last = value.substring(7, 11)
      return `${first}${middle ? ' ' : ''}${middle}${last ? ' ' : ''}${last}`
    }
  },
  methods: {
    toggleShowAgreement () {
      this.showAgreement = !this.showAgreement
      if (this.showAgreement) {
        this.isAgreementLoading = true
      }
    },
    sendSns () {
      if (this.isGlobalLoadingShow) {
        return
      }
      // TODO
      this.$refs.authorize.startCountdown()
    },
    onBackClick () {
      if (Utils.env.isAndroid()) {
        this.back()
      } else {
        history.go(-1)
      }
    },
    back () {
      if (Utils.env.isAndroid()) {
        nativeNavigator.hideKeyboard()
      }
      this.step = 1
      animation.transition(this.$refs.welcomeFrame, {
        styles: {
          transform: `translateX(0px)`
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      }, () => {})
      animation.transition(this.$refs.upperOne, {
        styles: {
          opacity: 1
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
      animation.transition(this.$refs.upperTwo, {
        styles: {
          opacity: 0
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
    },
    async inputPhoneAndRegion ({region, phone}) {
      if (Utils.env.isAndroid()) {
        nativeNavigator.hideKeyboard()
      }
      this.region = region
      this.phone = phone
      this.isGlobalLoadingShow = true
      try {
        const setPassResult = await Util.ajax({
          method: 'POST',
          login: false,
          data: {
            service: 'User.CheckPhoneNum',
            phone: this.phone
          }
        })
        if (setPassResult === 0) {
          this.loginByPassword()
        } else if (setPassResult === 1) {
          this.loginBySns()
        } else {
          throw new Error('User.CheckPhoneNum')
        }
        this.isGlobalLoadingShow = false
      } catch (e) {
        this.isGlobalLoadingShow = false
        modal.toast({
          message: e.message || '',
          duration: 3
        })
      }
    },
    loginByPassword () {
      this.loginMethod = 'password'
      this.transitionToNextStep()
    },
    async attemptPasswordLogin (password) {
      if (this.isGlobalLoadingShow) {
        return
      }
      this.isGlobalLoadingShow = true
      try {
        const userInfo = await Util.ajax({
          method: 'POST',
          login: false,
          data: {
            service: 'User.Login',
            phone: this.phone,
            password
          }
        })
        storage.setItem(Const.KEY_USER, JSON.stringify(userInfo), () => {
          this.isGlobalLoadingShow = false
          setTimeout(function () {
            Util.navigate('main')
          }, 50)
        })
      } catch (e) {
        this.isGlobalLoadingShow = false
        modal.toast({
          message: e.message || '',
          duration: 3
        })
      }
    },
    async attemptSnsLogin (snsCode) {
      if (this.isGlobalLoadingShow) {
        return
      }
      this.isGlobalLoadingShow = true
      try {
        const userId = await Util.ajax({
          method: 'POST',
          login: false,
          data: {
            service: 'User.LoginByVerifyCode',
            phone: this.phone,
            code: snsCode
          }
        })
        const userInfo = await Util.ajax({
          method: 'POST',
          login: false,
          data: {
            service: 'User.GetBaseInfo',
            userId
          }
        })
        storage.setItem(Const.KEY_USER, JSON.stringify(userInfo), () => {
          this.isGlobalLoadingShow = false
          setTimeout(function () {
            Util.navigate('main')
          }, 50)
        })
      } catch (e) {
        this.isGlobalLoadingShow = false
        modal.toast({
          message: e.message || '',
          duration: 3
        })
      }
    },
    loginBySns () {
      this.loginMethod = 'snsCode'
      this.transitionToNextStep()
      if (this.$refs.authorize.isCountdownInProgress() === false) {
        this.$refs.authorize.startCountdown()
      }
    },
    transitionToNextStep () {
      history.pushState(null, null)
      animation.transition(this.$refs.welcomeFrame, {
        styles: {
          transform: `translateX(${-690}px)`
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      }, () => {
        this.step = 2
      })
      animation.transition(this.$refs.upperOne, {
        styles: {
          opacity: 0
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
      animation.transition(this.$refs.upperTwo, {
        styles: {
          opacity: 1
        },
        duration: 300,
        timingFunction: 'cubic-bezier(0.25, 0.46, 0.45, 0.94)',
        delay: 0
      })
    },
    toggleLoginMethod () {
      if (Utils.env.isAndroid()) {
        nativeNavigator.hideKeyboard()
      }
      this.loginMethod = this.loginMethod === 'snsCode' ? 'password' : 'snsCode'
      if (this.$refs.authorize.isCountdownInProgress() === false) {
        this.$refs.authorize.startCountdown()
      }
    }
  },
  beforeDestroy () {
    if (typeof this.unbind === 'function') {
      window.removeEventListener('popstate', this.unbind)
    }
  },
  components: {
    Welcome,
    Authorize,
    WxcMask,
    PartLoading,
    WxcLoading
  }
}
</script>

<style scoped>
.welcome-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 750px;
  background-color: #fff;
}
.loading {
  position: absolute;
  left: 251px;
  top: 300px;
}
.back-btn {
  width: 21px;
  height: 36px;
  margin-top: 27px;
  margin-left: 32px;
}
.hidden {
  opacity: 0;
}
.upper {
  position: absolute;
  top: 160px;
  width: 750px;
  height: 180px;
  padding: 0 30px;
}
.upper-container {
  position: absolute;
  left: 30px;
  right: 30px;
  top: 0;
  bottom: 0;
}
.decorator {
  background-color: rgb(255, 241, 0);
  position: absolute;
  right: -375px;
  top: -375px;
  width: 750px;
  height: 750px;
  border-radius: 375px;
}
.welcome-title-main {
  font-weight: 500;
  font-size: 38px;
  padding: 20px 0;
}
.welcome-title-desc {
  font-size: 28px;
  color: #777;
}
.frame {
  position: absolute;
  top: 340px;
  left: 30px;
  right: 30px;
  bottom: 0;
  overflow: hidden;
}
.frame-wrapper {
  width: 1380px;
  flex-direction: row;
}
</style>



