import { Utils } from 'weex-ui'
import getJumpUrl from './get-jump-url'
export default function (pageName, spa = false, query) {
  if (Utils.env.isAndroid()) {
    const nativeNavigator = weex.requireModule('NativeNavigator')
    if (spa) {
      nativeNavigator.pushSpa(getJumpUrl(pageName, query))
    } else {
      nativeNavigator.push(getJumpUrl(pageName, query))
    }
  } else {
    location.href = getJumpUrl(pageName, query)
  }
}



